body {
  margin: 0;
  padding: 0;
}

.img {
  display: block;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-right: 20px;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.catalog {
  --columns: 1;
  grid-template-columns: repeat(var(--columns), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  font-family: Roboto, Arial, sans-serif;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    --columns: 2;
  }
}

@media (width >= 768px) {
  .catalog {
    --columns: 3;
  }
}

@media (width >= 1024px) {
  .catalog {
    --columns: 4;
  }
}

@media (width >= 1200px) {
  .catalog {
    --columns: 4;
  }
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  flex-direction: column;
  width: 200px;
  margin: auto;
  padding: 32px 16px 16px;
  transition: transform .3s;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  color: #060b35;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: color .3s;
}

.card:hover .card__title {
  color: #34568b;
}

.card__code {
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__reviews {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__reviews-amount {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__star:not(:last-child) {
  margin-right: 4px;
}

.card__stars-star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  display: inline-block;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  height: 18px;
  margin: 24px 0 16px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  padding: 11px 69px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  transition: background-color .3s, color .3s;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card__stars {
  justify-content: space-between;
  display: flex;
}

.card__stars-star {
  background: url("star-active.edda4c43.svg") center no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  display: inline-block;
}

.card__stars--active-4 .card__stars-star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}
